* {
    box-sizing: border-box;
}

.header {
    position: relative;
    background-color: black;
    margin-bottom: 30px;
}

.button__start-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100px;
    padding: 0 10px;
}

.button {
    width: 100%;
    padding: 5px 20px;
    background-color: rgb(0, 0, 0);
    color: white;
    font-weight: 700;
    cursor: pointer;
    font-size: 40px;
    transition: opacity 0.2s;
    border: none;
    text-align: left;
}

.button__start:hover {
    opacity: 0.8;
}

.button__option-wrapper {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    height: 100%;
    align-items: center;
}

.button--option {
    font-size: 25px;
}

.button:hover {
    opacity: 0.8;
}

.records-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.records {
    color: rgb(0, 0, 0);
    font-size: 20px;
}

.count {
    width: 50%;
    color: white;
    font-size: 33px;
    padding: 5px 20px;
}

.time {
    padding: 5px 20px;
    width: 50%;
    color: white;
    font-size: 33px;
}

.card {
    position: relative;
    display: flex;
    width: 100%;
    flex-wrap: wrap
}

.card__front {
    flex-basis: 25%;
    position: relative;
    width: 100%;
    height: 150px;
    overflow: hidden;  
    padding-top: 25%;
    background-color:rgb(236, 149, 49);
    /* border: 1px solid black; */
}

.card__back {
    position: relative;
    flex-basis: 25%;
    overflow: hidden;
    z-index: 1;
}

.card__front-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 2;

}

.loader {
    position: absolute;
    border: 9px solid #adabab;
    border-radius: 50%;
    left: 22%;
    top: 22%;
    border-top: 9px solid #3d3d3d;
    width: 80px;
    height: 80px;
    animation: spin 1s linear infinite;
    z-index: 1;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    30% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.card__back-button {
    width: 100%;
    height: 150px;
    background-color:rgb(236, 149, 49);
    cursor: pointer;
    overflow: hidden;
    color:rgb(236, 149, 49);
    font-size: 20px;
    font-weight: 700;
    border: 1px solid black;
}

.heading {
    text-align: center;
    background-color: rgb(255, 255, 255);
    color: white;
    font-size: 40px;
    height: 50px;
    margin: 5px 0;
}

.heading-show {
    background-color: black;
}

.game {
    width: 600px;

}

.medium {
    width: 900px;
}

.card__front--medium {
    flex-basis: 16.66666%;
    padding-top: 16.66666%;
}

.card__back--medium {
    flex-basis: 16.66666%
}

.large {
    width: 1000px;
}

.card__front--large {
    flex-basis: 10%;
    height: 100px;
    padding-top: 10%;
}
.card__front--large .loader{
    border: 6px solid #adabab;
    border-top: 9px solid #3d3d3d;
    left: 25%;
    top: 25%;
    width: 50px;
    height: 50px;
 
}
.card__back--large {
    flex-basis: 10%;
    height: 100px;

}

.winner-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
}

.winner {
    position: relative;
    margin: auto 0;
    /* opacity: 1; */
    font-size: 55px;
    animation: blinkingText 1.2s infinite;
    padding: 30px;
    color: rgb(255, 255, 255, 1);
}

@keyframes blinkingText {
    0% {
        color: rgb(255, 255, 255, 1);
    }
    50% {
        color:rgb(236, 149, 49);
    }
    100% {
        color: rgb(255, 255, 255, 1);
    }
}

.start-counter-wrapper{
    position: absolute;
    background-color: rgb(0, 0, 0);
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color:rgb(236, 149, 49);
    z-index: 3;
    font-size: 300px;
}
.button--record{
    color: black
}

.records-wrapper{
    position: absolute;
    top: 0;
    right: 0;
    width: 0%;
    height: 0;
    transition: width 2s;
    z-index: 2;
}
.records-wrapper.active {
    height: 100%;
    background-color: rgb(212, 212, 212, 0.3);
    transition: height 0.5s;
}

.records {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 0;
    transition: width 2s;
    background-color: rgb(236, 149, 49, 0.7);
    z-index: 1;
}
.records.active {
    height: 40%;
}